body {
  background-color: var(--colors-background-primary);
  font-family: var(--typography-font-family-primary);
}

.src-css-__main__grid--3dd0W {
  display: grid;
  /* stylelint-disable-next-line unit-whitelist */
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-template-rows: repeat(1, 1fr);
}

.src-css-__main__grid__card--11aRf {
  box-shadow: 0 0 3rem -1rem rgba(0, 0, 0, 0.5);
  margin: 1rem;
  position: relative;
  transition: transform 0.3s ease-in-out, box-shadow 0.1s;
}

.src-css-__main__grid__card--11aRf:hover {
  box-shadow: 0 0.5em 3rem -1rem rgba(0, 0, 0, 0.5);
  transform: translateY(-0.5rem) scale(1.0125);
}

/* Lazy Load Styles */
.src-css-__main__grid__card__image--1o_Ck {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  filter: blur(0.2rem); /* blur the lowres image */
}

.src-css-__main__grid__card__image--1o_Ck > img {
  display: block;
  opacity: 0; /* visually hide the img element */
  width: 100%;
}

.src-css-__main__grid__card__image--is-loaded--3Lc_h {
  filter: none; /* remove the blur on fullres image */
  transition: filter 1s;
}

.src-css-__main__grid__card__image--is-loaded--3Lc_h > img {
  max-width: 100%;
}

.src-css-__main__grid__card__wrapper--2r8y9 {
  width: 100%;
  z-index: 100;
  position: absolute;
  color: var(--colors-font-primary);
  font-weight: bold;
  text-align: center;
  top: 50%;
}

.src-css-__main__viz-list--xgJH3 {
  outline: 0.2rem solid green;
}

.src-css-__main__viz-list-item--1CURT {
  color: red;
}
